.post {
  cursor: pointer;
  display: block;
  margin-bottom: 28px;
}
.post .img {
  margin-bottom: 18px;
  position: relative;
}
.post .img::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 101.5%;
  border-radius: 4px;
  background-color: rgba(0, 163, 255, 0.05);
}
.post .img img {
  display: block;
  width: 100%;
}
.post .title {
  color: #10a7ff;
  font-size: 1.17em;
  margin-bottom: 7px;
}
.post .text {
  font-size: 0.94em;
  margin-bottom: 12px;
  height: 55px;
  overflow: hidden;
  padding-bottom: 5px;
}
.post .tag {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
  padding-top: 7px;
  border-top: 1px solid #e6e6e6;
}
.post .tag .category {
  color: #10a7ff;
  text-transform: capitalize;
  font-weight: 500;
  text-align: center;
  line-height: 17px;
}
.post .tag .date {
  color: #C4C4C4;
}
.post:hover > .title {
  color: #5581F1;
  text-decoration: underline;
}