.banner {
  width: 100%;
  padding: 100px 0;
  background: linear-gradient(135deg, #24c6dc 0%, #57519b 100%);
  color: #FEFEFE;
  font-weight: bold;
  font-size: 2.32em;
  text-align: center;
  border-radius: 4px;
  margin-bottom: 30px;
}

.newsAndSidebar {
  display: flex;
  justify-content: space-between;
  position: relative;
}
.newsAndSidebar .news {
  width: 600px;
}
.newsAndSidebar .news .newsTitle {
  color: #141414;
  margin-bottom: 20px;
}
.newsAndSidebar .news .link {
  text-decoration: none;
  color: #242424;
}
.newsAndSidebar .sidebar {
  width: 215px;
  padding-left: 20px;
  border-left: 1px solid #e6e6e6;
}
.newsAndSidebar .sidebar .events {
  background-color: #fefefe;
  z-index: 2;
  margin: 0 auto;
  position: sticky;
  top: 103px;
  margin-bottom: 20px;
}
.newsAndSidebar .sidebar .events .subtitle {
  color: #00C0FF;
  margin-bottom: 10px;
  padding: 0.1em 0;
  font-size: 1.1em;
}
.newsAndSidebar .sidebar .events .event {
  margin-bottom: 7px;
}
.newsAndSidebar .sidebar .events .event .eventTitle {
  font-weight: 500;
  font-size: 0.94em;
  margin-bottom: 5px;
}
.newsAndSidebar .sidebar .events .event .eventPlace {
  display: block;
  font-size: 0.88em;
  color: #C4C4C4;
  font-weight: 500;
  margin-bottom: 1px;
}
.newsAndSidebar .sidebar .events .event .eventDate {
  font-size: 0.88em;
  color: #C4C4C4;
  font-weight: 500;
  margin-bottom: 1px;
}
.newsAndSidebar .sidebar .events .event .eventTime {
  float: right;
  font-size: 0.88em;
  color: #C4C4C4;
  font-weight: 500;
}
.newsAndSidebar .sidebar .feedback {
  position: sticky;
  top: 395px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-bottom: 18px;
}
.newsAndSidebar .sidebar .feedback .feedbackLink {
  text-decoration: none;
  color: #00b1eb;
  margin-bottom: 8px;
}
.newsAndSidebar .sidebar .feedback .feedbackLink:hover {
  text-decoration: underline;
}

@media screen and (max-width: 1250px) {
  .banner {
    padding: 48px 0;
    margin-bottom: 22px;
  }

  .newsAndSidebar {
    flex-direction: column-reverse;
  }
  .newsAndSidebar .news {
    width: 100%;
  }
  .newsAndSidebar .sidebar {
    padding: 0;
    width: 100%;
    border-left: none;
    border-bottom: 1px solid #e6e6e6;
    margin-bottom: 18px;
  }
  .newsAndSidebar .sidebar .events {
    width: 100%;
    margin: 0;
    position: static;
    margin-bottom: 10px;
  }
  .newsAndSidebar .sidebar .events .subtitle {
    padding: 5px 0;
    margin-bottom: 4px;
  }
  .newsAndSidebar .sidebar .events .event {
    display: inline-block;
    margin-bottom: 7px;
    width: 154px;
  }
  .newsAndSidebar .sidebar .events .event:nth-child(3n) {
    float: right;
  }
  .newsAndSidebar .sidebar .feedback {
    position: static;
    width: 100%;
    margin-bottom: 5px;
    flex-direction: row;
  }
}
@media screen and (max-width: 335px) {
  .newsAndSidebar {
    flex-direction: column-reverse;
  }
  .newsAndSidebar .news {
    width: 100%;
  }
  .newsAndSidebar .sidebar .events .event {
    width: 145px;
  }
}