.title {
  font-size: 1.52em;
  font-weight: 400;
  margin-bottom: 14px;
}

.adminPosition {
  border-bottom: 1px solid #e6e6e6;
  border-radius: 4px;
  padding: 7px;
  padding-bottom: 15px;
  margin-bottom: 18px;
}
.adminPosition:last-child {
  border: none;
}
.adminPosition .who {
  font-weight: 500;
  margin-bottom: 3px;
}
.adminPosition .position {
  color: #bababa;
  margin-bottom: 10px;
}
.adminPosition .text {
  color: #242424;
  margin-bottom: 5px;
}
.adminPosition .phone {
  text-decoration: none;
  color: #00C0FF;
}
.adminPosition .phone:hover {
  text-decoration: underline;
}