@import "../../colors.scss";

.banner {
  width: 100%;
  padding: 100px 0;
  background: linear-gradient(135deg, #24c6dc 0%, #57519b 100%);
  color: $white;
  font-weight: bold;
  font-size: 2.32em;
  text-align: center;
  border-radius: 4px;
  margin-bottom: 30px;
}

.newsAndSidebar {
  display: flex;
  justify-content: space-between;
  position: relative;
  .news {
    width: 600px;
    .newsTitle {
      color: $black;
      margin-bottom: 20px;
    }
    .link {
      text-decoration: none;
      color: $text;
    }
  }
  .sidebar {
    width: 215px;
    padding-left: 20px;
    border-left: 1px solid $border;
    .events {
      background-color: #fefefe;
      z-index: 2;
      margin: 0 auto;
      position: sticky;
      top: 103px;
      margin-bottom: 20px;
      .subtitle {
        color: $light-blue;
        margin-bottom: 10px;
        padding: 0.1em 0;
        font-size: 1.1em;
      }
      .event {
        margin-bottom: 7px;
        .eventTitle {
          font-weight: 500;
          font-size: 0.94em;
          margin-bottom: 5px;
        }
        .eventPlace {
          display: block;
          font-size: 0.88em;
          color: $gray;
          font-weight: 500;
          margin-bottom: 1px;
        }
        .eventDate {
          font-size: 0.88em;
          color: $gray;
          font-weight: 500;
          margin-bottom: 1px;
        }
        .eventTime {
          float: right;
          font-size: 0.88em;
          color: $gray;
          font-weight: 500;
        }
      }
    }
    .feedback {
      position: sticky;
      top: 395px;
      margin: 0 auto;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      margin-bottom: 18px;
      .feedbackLink {
        text-decoration: none;
        color: darken($light-blue, 4);
        margin-bottom: 8px;
        &:hover {
          text-decoration: underline;
        }
      }
    }
  }
}

@media screen and (max-width: 1250px) {
  .banner {
    padding: 48px 0;
    margin-bottom: 22px;
  }
  .newsAndSidebar {
    flex-direction: column-reverse;
    .news {
      width: 100%;
    }
    .sidebar {
      padding: 0;
      width: 100%;
      border-left: none;
      border-bottom: 1px solid $border;
      margin-bottom: 18px;
      .events {
        width: 100%;
        margin: 0;
        position: static;
        margin-bottom: 10px;
        .subtitle {
          padding: 5px 0;
          margin-bottom: 4px;
        }
        .event {
          display: inline-block;
          margin-bottom: 7px;
          width: 154px;
          &:nth-child(3n) {
            float: right;
          }
        }
      }
      .feedback {
        position: static;
        width: 100%;
        margin-bottom: 5px;
        flex-direction: row;
      }
    }
  }
}

@media screen and (max-width: 335px) {
  .newsAndSidebar {
    flex-direction: column-reverse;
    .news {
      width: 100%;
    }
    .sidebar {
      .events {
        .event {
          width: 145px;
        }
      }
    }
  }
}
