@import "../../../colors.scss";

.title {
  font-size: 1.52em;
  font-weight: 400;
  margin-bottom: 14px;
}

.adminPosition {
  border-bottom: 1px solid $border;
  border-radius: 4px;
  padding: 7px;
  padding-bottom: 15px;
  margin-bottom: 18px;
  &:last-child {
    border: none;
  }
  .who {
    font-weight: 500;
    margin-bottom: 3px;
  }
  .position {
    color: darken($gray, 4);
    margin-bottom: 10px;
  }
  .text {
    color: $text;
    margin-bottom: 5px;
  }
  .phone {
    text-decoration: none;
    color: $light-blue;
    &:hover {
      text-decoration: underline;
    }
  }
}
