@import "../../colors.scss";

.post {
  cursor: pointer;
  display: block;
  margin-bottom: 28px;
  .img {
    margin-bottom: 18px;
    position: relative;
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 101.5%;
      border-radius: 4px;
      background-color: rgba(#00a3ff, 0.05);
    }
    img {
      display: block;
      width: 100%;
    }
  }
  .title {
    color: $link;
    font-size: 1.17em;
    margin-bottom: 7px;
  }
  .text {
    font-size: 0.94em;
    margin-bottom: 12px;
    height: 55px;
    overflow: hidden;
    padding-bottom: 5px;
  }
  .tag {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    padding-top: 7px;
    border-top: 1px solid $border;
    .category {
      color: $link;
      text-transform: capitalize;
      font-weight: 500;
      text-align: center;
      line-height: 17px;
    }
    .date {
      color: $gray;
    }
  }
  &:hover > .title {
    color: $dark-blue;
    text-decoration: underline;
  }
}
